special {
 font-family: "special";
 font-size: large;
 color: white;

}






.resume {
 display: grid;
 align-content: center;
}

/* unvisited link */
a:link {
 color: beige;
}

/* visited link */
a:visited {
 color: tan;
}

/* mouse over link */
a:hover {
 color: hotpink;
}

/* selected link */
a:active {
 color: blue;
}









.banner {
 background: repeating-linear-gradient(60deg, darkkhaki, darkolivegreen);
}
#name{
 text-align: center;
 font-weight: bold;
 color: yellowgreen;
text-shadow: 5px 5px 7px rgba(0.5,0.5,0.5,0.5);

}





.ribbon-highlight {
 position: relative;
 margin: 0.1em;
}

.section {
 display: grid;
 place-items: center;
 align-content: center;
 min-height: 100vh;
}

.ribbon-highlight:before {
 z-index: -1;
 left: -0.5em;
 top: 0.1em;
 border-width: 0.5em;
 border-style: solid;
 border-color: lightgreen;
 position: absolute;
 width: 100%;
 border-left-color: transparent;
 border-right-color: transparent;
}

.circle-sketch-highlight {
 position: relative;
}

